// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Credentials from '@/services/Credentials.service.js'
import VueResource from 'vue-resource'
import {address} from '@/constants/address.js'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify)
Vue.config.productionTip = false
Vue.use(VueResource)

const credentials = new Credentials()
Vue.http.interceptors.push(function (request, next) {
  request.headers.set('Authorization', 'Bearer ' + credentials.getToken())
  request.headers.set('Content-Type', 'application/json')
  let vm = this
  next(function (response) {
    if (response.status === 401) {
      console.log('CREDENCIALES BORRADAS')
      credentials.clearCredentials()
      window.location.href = (address.spa)
      vm.$dispatch('logout')
    }
  })
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



