<template>
  <v-flex class="center">
  <v-card >
    <v-card-title>
      Cepas
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        v-model="search"
      ></v-text-field>
    </v-card-title>
    <v-data-table
        v-bind:headers="headers"
        v-bind:items="items"
        v-bind:search="search"
      >
      <template slot="items" slot-scope="props">
        <td class="text-xs-left">{{ props.item.codigo_cepa }}</td>
        <td class="text-xs-left">{{ props.item.nombre_cientifico }}</td>
        <td class="text-xs-right">{{ props.item.tipo_microorganismo }}</td>
        <td>
          <v-btn color="primary" flat @click.native.stop="id = props.item.id, verCepa(id), verDetalle = !verDetalle"><v-icon>search</v-icon>Details</v-btn>
        </td>
      </template>
      <template slot="pageText" slot-scope="{ pageStart, pageStop }">
        From {{ pageStart }} to {{ pageStop }}
      </template>
    </v-data-table>
  </v-card>
    <div>
      <v-btn class="text-xs-left" @click.native.stop="login()">Log in<v-icon></v-icon></v-btn>
    </div>

    <!--Detalles -->
    <v-dialog v-model="verDetalle" scrollable max-width="800px">
        <v-card>
        <v-card-title>
          <span class="headline">Detalles</span>
        </v-card-title>
        <v-card-text>
          <v-card color="grey lighten-4">
            <v-card-text>
              <v-subheader><h2>CODIGO: {{detalleCepa.codigo_cepa}}</h2></v-subheader>
              <v-subheader><h2>Fecha Registro: {{detalleCepa.fecha_ingreso}}</h2></v-subheader>
            </v-card-text>
          </v-card>
          <v-card flat>
            <v-card-text>
              <v-subheader>Nombre Cientifico: {{detalleCepa.nombre_cientifico}}</v-subheader>
              <v-subheader>Tipo de microorganismo: {{detalleCepa.tipo_microorganismo}}</v-subheader>
              <v-subheader>Origen: {{detalleCepa.origen}}</v-subheader>
              <v-subheader>Depositante: {{detalleCepa.depositante}}</v-subheader>
              <v-subheader>Substrato hopedero: {{detalleCepa.substrato_hospedero}}</v-subheader>
              <v-subheader>Temperatura Incubación: {{detalleCepa.temperatura_incubacion}}</v-subheader>
              <v-subheader>Tiempo de Incubación: {{detalleCepa.tiempo_incubacion}}</v-subheader>
              <v-subheader>Metabolismo: {{detalleCepa.metabolismo}}</v-subheader>
              <v-subheader>Metodo Preservacion: {{detalleCepa.metodo_preservacion}}</v-subheader>
              <v-subheader>Metodo Identificacion: {{detalleCepa.metodo_identificacion}}</v-subheader>
              <v-subheader>Institucion aisladora: {{detalleCepa.institucion_aisladora}}</v-subheader>
              <v-subheader>Requerimientos Especiales: {{detalleCepa.requerimientos_especiales}}</v-subheader>
            </v-card-text>
          </v-card>
        </v-card-text>
        <v-card-actions style="height: 60px;">
          <v-spacer></v-spacer>
          <v-btn color="green" flat @click.native="verDetalle = false" fixed>Done</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

</v-flex>
</template>


<script>
import {registroService} from '@/services/Registro.service.js'
import {address} from '@/constants/address.js'

export default {
  data () {
    return {
      verDetalle: false,
      max25chars: (v) => v.length <= 25 || 'Input too long!',
      tmp: '',
      search: '',
      pagination: {},
      headers: [
        {
          text: 'Registration Number',
          align: 'left',
          sortable: true,
          value: 'codigo_cepa'
        },
        { text: 'Scientific Name', sortable: false, align: 'left', value: 'nombre_cientifico' },
        { text: 'Microorganism Type', sortable: false, align: 'left', value: 'tipo_microorganismo' },
        { text: 'Options', sortable: false, value: 'id' }
      ],
      items: [],
      tipos: [
        {text: 'Bacteria', value: 'Bacteria'},
        {text: 'Hongo', value: 'Hongo'},
        {text: 'Levadura', value: 'Levadura'}
      ],
      metabolismos: [
        {text: 'Aerobio', value: 'Aerobio'},
        {text: 'Anaerobio facultativo', value: 'Anaerobio facultativo'},
        {text: 'Microaerófilo', value: 'Microaerófilo'},
        {text: 'Anaerobio estricto', value: 'Anaerobio estricto'}
      ],
      id: 0,
      detalleCepa: {}
    }
  },
  mounted () {
    registroService.query().then(data => {
      this.items = data.body
    })
  },
  methods: {
    verCepa (id) {
      this.$http.get(address.api + 'publico/' + this.id).then(response => {
        this.detalleCepa = response.body;
        }, response => {
        // error callback 
      });
    },
    login () {
      this.$router.push('/login')
    },

  }
}
</script>


