<template>
  <v-flex class="center">
  <v-card >
    <v-card-title>
      Cepas
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        v-model="search"
      ></v-text-field>
    </v-card-title>
    <v-data-table
        v-bind:headers="headers"
        v-bind:items="items"
        v-bind:search="search"
      >
      <template slot="items" slot-scope="props">
        <td class="text-xs-left">{{ props.item.codigo_cepa }}</td>
        <td class="text-xs-left">{{ props.item.nombre_cientifico }}</td>
        <td class="text-xs-right">{{ props.item.tipo_microorganismo }}</td>
        <td class="text-xs-right">
          <!--   Botones agregar/quitar   -->
          <!-- 
          <v-btn flat @click.native.stop="id = props.item.id, verCepa(id), ingresoPassword = !ingresoPassword"><v-icon>remove</v-icon></v-btn>
          -->
          {{ props.item.stock }}
          <!-- 
          <v-btn flat @click.native.stop="id = props.item.id, ingresoPassword = !ingresoPassword"><v-icon>add</v-icon></v-btn>
          -->
        </td>
        <td>
          <v-btn color="primary" flat @click.native.stop="id = props.item.id, verCepa(id), verDetalle = !verDetalle"><v-icon>search</v-icon>Details</v-btn>
          <v-btn color="green" flat @click.native.stop="id = props.item.id, verCepa(id), editarRegistro = !editarRegistro"><v-icon>mode_edit</v-icon>Edit</v-btn>
          <v-btn color="error" flat @click.native.stop="id = props.item.id, verConfirmacion = !verConfirmacion"><v-icon>delete_forever</v-icon>Delete</v-btn>
        </td>
      </template>
      <template slot="pageText" slot-scope="{ pageStart, pageStop }">
        From {{ pageStart }} to {{ pageStop }}
      </template>
    </v-data-table>
    <div class="text-xs-right pt-5">
    <v-btn outline  fab color="green darken-4" @click.native.stop="agregarRegistro = !agregarRegistro">
      <v-icon>add</v-icon>
    </v-btn>
  </div>
  </v-card>
    <div>
      <v-btn class="text-xs-left" @click.native.stop="logout()">Log out<v-icon></v-icon></v-btn>
    </div>

  <!--Nav agregar -->
      <v-dialog v-model="agregarRegistro" max-width="800px">
        <v-card>
        <v-card-title>
          <span class="headline">Register Strain</span>
        </v-card-title>
        <v-card-text>
          <v-form action="crearCepa(model)">
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field label="Scientific Name" v-model="model.nombre_cientifico" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Registration Number" v-model="model.codigo_cepa" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Registration Date" v-model="model.fecha_ingreso" type="date" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Substrato Hospedero" v-model="model.substrato_hospedero" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Metodo Preservación" v-model="model.metodo_preservacion" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Metodo Identificacion" v-model="model.metodo_identificacion" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-select label="Tipo de Microorganismo" v-bind:items="tipos" v-model="model.tipo_microorganismo" required>
                </v-select>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Origen" v-model="model.origen" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Institucion aisladora" v-model="model.institucion_aisladora" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Temperatura de incubacion" v-model="model.temperatura_incubacion" type="number" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Tiempo de incubacion" v-model="model.tiempo_incubacion" type="number" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-select label="Tipo de Metabolismo" v-bind:items="metabolismos" v-model="model.metabolismo" required>
                </v-select>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Requerimientos especiales" v-model="model.requerimientos_especiales" textarea></v-text-field>
              </v-flex>
              <v-flex xs12 md6>
                <v-radio-group v-model="model.cepa_tipo" label="Es Cepa Tipo:" row>
                  <v-radio label="Si"
                    color="blue darken-3" value="true"></v-radio>
                  <v-radio label="No"
                    color="red darken-3" value="false" default></v-radio>
                </v-radio-group>
              </v-flex>
              <v-flex xs12 md6>
                <v-checkbox label="Resistenete a la Congelacion" v-model="model.resiste_congelacion" light></v-checkbox>
              </v-flex>
              <v-flex xs12 md6>
                <v-checkbox label="Es publico" v-model="model.publico" light></v-checkbox>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Name of the depositor" v-model="model.depositante" required></v-text-field>
              </v-flex>
            </v-layout>
          </v-form>
          <small>*Campos requeridos</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn type="submit" color="green" flat @click.native="crearCepa(model), agregarRegistro = false">Guardar</v-btn>
          <v-btn color="red" flat @click.native="agregarRegistro = false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--Nav editar -->
      <v-dialog v-model="editarRegistro" max-width="800px">
        <v-card>
        <v-card-title>
          <span class="headline">Register Strain</span>
        </v-card-title>
        <v-card-text>
          <v-form action="editarCepa(model)">
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field label="Scientific Name" v-model="detalleCepa.nombre_cientifico" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Registration Number" v-model="detalleCepa.codigo_cepa" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Registration Date" v-model="detalleCepa.fecha_ingreso" type="date" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Substrato Hospedero" v-model="detalleCepa.substrato_hospedero" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Metodo Preservación" v-model="detalleCepa.metodo_preservacion" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Metodo Identificacion" v-model="detalleCepa.metodo_identificacion" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-select label="Tipo de Microorganismo" v-bind:items="tipos" v-model="detalleCepa.tipo_microorganismo" required>
                </v-select>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Origen" v-model="detalleCepa.origen" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Institucion aisladora" v-model="detalleCepa.institucion_aisladora" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Temperatura de incubacion" v-model="detalleCepa.temperatura_incubacion" type="number" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Tiempo de incubacion" v-model="detalleCepa.tiempo_incubacion" type="number" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-select label="Tipo de Metabolismo" v-bind:items="metabolismos" v-model="detalleCepa.metabolismo" required>
                </v-select>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Requerimientos especiales" v-model="detalleCepa.requerimientos_especiales" textarea></v-text-field>
              </v-flex>
              <v-flex xs12 md6>
                <v-checkbox label="Resistenete a la Congelacion" v-model="detalleCepa.resiste_congelacion" light></v-checkbox>
              </v-flex>
              <v-flex xs12 md6>
                <v-checkbox label="Show to public" v-model="detalleCepa.publico" light></v-checkbox>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Stock" v-model="detalleCepa.stock" type="number" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Name of the depositor" v-model="detalleCepa.depositante" required></v-text-field>
              </v-flex>
            </v-layout>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn type="submit" color="green" flat @click.native="editarCepa(detalleCepa), editarRegistro = false">Update</v-btn>
          <v-btn color="red" flat @click.native="editarRegistro = false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--Detalles -->
    <v-dialog v-model="verDetalle" scrollable max-width="800px">
        <v-card>
        <v-card-title>
          <span class="headline">Detalles</span>
        </v-card-title>
        <v-card-text>
          <v-card color="grey lighten-4">
            <v-card-text>
              <v-subheader><h2>CODIGO: {{detalleCepa.codigo_cepa}}</h2></v-subheader>
              <v-subheader><h2>Fecha Registro: {{detalleCepa.fecha_ingreso}}</h2></v-subheader>
            </v-card-text>
          </v-card>
          <v-card flat>
            <v-card-text>
              <v-subheader>Nombre Cientifico: {{detalleCepa.nombre_cientifico}}</v-subheader>
              <v-subheader>Tipo de microorganismo: {{detalleCepa.tipo_microorganismo}}</v-subheader>
              <v-subheader>Origen: {{detalleCepa.origen}}</v-subheader>
              <v-subheader>Depositante: {{detalleCepa.depositante}}</v-subheader>
              <v-subheader>Substrato hopedero: {{detalleCepa.substrato_hospedero}}</v-subheader>
              <v-subheader>Temperatura Incubación: {{detalleCepa.temperatura_incubacion}}</v-subheader>
              <v-subheader>Tiempo de Incubación: {{detalleCepa.tiempo_incubacion}}</v-subheader>
              <v-subheader>Metabolismo: {{detalleCepa.metabolismo}}</v-subheader>
              <v-subheader>Metodo Preservacion: {{detalleCepa.metodo_preservacion}}</v-subheader>
              <v-subheader>Metodo Identificacion: {{detalleCepa.metodo_identificacion}}</v-subheader>
              <v-subheader>Institucion aisladora: {{detalleCepa.institucion_aisladora}}</v-subheader>
              <v-subheader>Requerimientos Especiales: {{detalleCepa.requerimientos_especiales}}</v-subheader>
            </v-card-text>
          </v-card>
        </v-card-text>
        <v-card-actions style="height: 60px;">
          <v-spacer></v-spacer>
          <v-btn color="green" flat @click.native="verDetalle = false" fixed>Done</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--Dialogo Confirmacion Eliminar -->
    <v-dialog v-model="verConfirmacion" max-width="350px">
        <v-card>
        <v-card-title>
          <span class="headline">Delete</span>
        </v-card-title>
        <v-card-text>
          <v-card color="grey lighten-4">
            <v-card-text>
              <v-subheader><h2>Esta Seguro?</h2></v-subheader>
            </v-card-text>
          </v-card>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click.native.stop="eliminarCepa(id), verConfirmacion = !verConfirmacion">Delete</v-btn>
          <v-btn color="primary" flat @click.native="verConfirmacion = false">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--Dialogo Ingresar Pass para incrementar/reducir stock -->
    <v-dialog v-model="ingresoPassword" max-width="350px">
        <v-card>
        <v-card-title>
          <span class="headline">Delete</span>
        </v-card-title>
        <v-card-text>
          <v-card color="grey lighten-4">
            <v-card-text>
              <v-subheader><h2>Esta Seguro?</h2></v-subheader>
            </v-card-text>
          </v-card>
        </v-card-text>
        <v-flex xs12>
                <v-text-field type="password" label="Contraseña para modificar stock" v-model="passLocal" required></v-text-field>
              </v-flex>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click.native.stop="cambiarStock(id), ingresoPassword = !ingresoPassword">Confirmar</v-btn>
          <v-btn color="primary" flat @click.native="ingresoPassword = false">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

</v-flex>
</template>




<script>
import {cepaService} from '@/services/Cepa.service.js'
import {address} from '@/constants/address.js'
import CredentialsServices from '@/services/Credentials.service.js'

export default {
  data () {
    return {
      agregarRegistro: false,
      verDetalle: false,
      verConfirmacion: false,
      editarRegistro: false,
      ingresoPassword: false,
      passLocal: '',
      credentialService: new CredentialsServices(),
      max25chars: (v) => v.length <= 25 || 'Input too long!',
      tmp: '',
      search: '',
      pagination: {},
      headers: [
        {
          text: 'Registration Number',
          align: 'left',
          sortable: true,
          value: 'codigo_cepa'
        },
        { text: 'Scientific Name', sortable: false, align: 'left', value: 'nombre_cientifico' },
        { text: 'Microorganism Type', sortable: false, align: 'left', value: 'tipo_microorganismo' },
        { text: 'Stock', sortable: false, align: 'left', value: 'stock' },
        { text: 'Options', sortable: false, value: 'id' }
      ],
      items: [],
      model: {
        nombre_cientifico: '',
        codigo_cepa: '',
        fecha_ingreso: '',
        substrato_hospedero: '',
        depositante: 'depositante de prueba',
        metodo_identificacion: '',
        metodo_preservacion: '',
        tipo_microorganismo: '',
        origen: '',
        institucion_aisladora: '',
        temperatura_incubacion: '',
        tiempo_incubacion: '',
        metabolismo: '',
        requerimientos_especiales: '',
        resiste_congelacion: false,
        publico: false,
        cepa_tipo: false
      },
      tipos: [
        {text: 'Bacteria', value: 'Bacteria'},
        {text: 'Hongo', value: 'Hongo'},
        {text: 'Levadura', value: 'Levadura'}
      ],
      metabolismos: [
        {text: 'Aerobio', value: 'Aerobio'},
        {text: 'Anaerobio facultativo', value: 'Anaerobio facultativo'},
        {text: 'Microaerófilo', value: 'Microaerófilo'},
        {text: 'Anaerobio estricto', value: 'Anaerobio estricto'}
      ],
      id: 0,
      detalleCepa: {}
    }
  },
  mounted () {
    cepaService.query().then(data => {
      this.items = data.body
    })
  },
  methods: {
    crearCepa (model) {
        console.log('TIPO')
        console.log(this.model.cepa_tipo)
      cepaService.save(this.model).then(data => {
        this.items.push(model)
        cepaService.query().then(data => {
          this.items = data.body
          })
      }, err => {
        console.log('--ERROR--')
        console.log(err)
        if (err.status) {
          alert(err.body)
        }
      })
    },
    verCepa (id) {
      this.$http.get(address.api + 'cepas/' + this.id).then(response => {
        this.detalleCepa = response.body;
        }, response => {
        // error callback 
      });
    },
    eliminarCepa (id) {
      this.$http.delete(address.api + 'cepas/' + this.id).then(response => {
        this.detalleCepa = response.body;
        // console.log('--ID_DELETE--' + this.id)
        cepaService.query().then(data => {
          this.items = data.body
          })
        }, response => {
        // error callback 
      });
    },
    editarCepa (id) {
      this.$http.put(address.api + 'cepas/' + this.id, this.detalleCepa).then(response => {
        this.detalleCepa = response.body;
        cepaService.query().then(data => {
          this.items = data.body
          })
        }, response => {
        // error callback 
      });
    },
    logout () {
      this.credentialService.clearCredentials()
      this.$router.push('/publico')
    },
    cambiarStock (id) {
      this.$http.put(address.api + 'editarStock/' + this.id, this.passLocal).then(response => {
        console.log('PASS : ' + this.passLocal)
        console.log('RESP : ' + response)
        cepaService.query().then(data => {
          this.items = data.body
          })
        }, response => {
        // error callback 
      });
    }

  }
}
</script>


