import Vue from 'vue'
import Router from 'vue-router'
import Cepas from '@/components/Cepas'
import Publico from '@/components/Publico'
import Login from '@/components/Login'
import DashBoard from '@/components/DashBoard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DashBoard',
      component: DashBoard,
      redirect: '/publico',
      children: [
        {
          path: '/login',
          name: 'Login',
          component: Login
        },
        {
          path: '/cepas',
          name: 'Cepas',
          component: Cepas
        },
        {
          path: '/publico',
          name: 'Publico',
          component: Publico
        }
      ]
    }
  ]
})



